<template>
	<div class="message is-primary">
		<p class="message-header">
			{{ title }}
			<button
				class="button is-small question is-link is-light"
				aria-label="help"
			>
				<span>
					<font-awesome-icon icon="question" />
				</span>
			</button>
		</p>
		<div class="message-body">
			<div class="field">
				<div class="control">
					<label for="pair">Cặp tiền tệ</label>
				</div>
				<div class="control has-icons-left">
					<div class="select is-fullwidth">
						<select
							name="pair"
							id="pair"
							v-model="pairIndex"
							class="is-fullwidth"
						>
							<option
								v-for="(pair, index) in pairs"
								:key="pair.index"
								v-bind:value="index"
							>
								{{ pair.name }}
							</option>
						</select>
					</div>
					<span class="icon is-left">
						<font-awesome-icon icon="coins" />
					</span>
				</div>
			</div>
			<div class="field columns is-mobile">
				<div class="column is-mobile">
					<div class="control">
						<label for="point">Điểm vào lệnh</label>
						<input
							class="input"
							name="point"
							type="text"
							inputmode="decimal"
							autocomplete="off"
							v-model.number="entry"
							@focus="$event.target.select()"
						/>
					</div>
				</div>
				<div class="column">
					<div class="control is-mobile">
						<label for="point">Điểm stoploss</label>
						<input
							class="input"
							name="point"
							type="text"
							inputmode="decimal"
							autocomplete="off"
							v-model.number="stoploss"
							@focus="$event.target.select()"
						/>
					</div>
				</div>
			</div>
			<div class="field">
				<volume-input
					v-on:volume-changed="volumeInputValueChanged"
				></volume-input>
			</div>
		</div>
	</div>
</template>

<script>
import VolumeInput from "@/components/VolumeInput.vue"

export default {
	components: { VolumeInput },
	props: ["title", "pairs"],
	data() {
		return {
			message: "Hello",
			point: 1000,
			entry: 1234.56,
			stoploss: 1234.67,
			float: 100,
			volume: 0.01,
			pairIndex: 0,
			bufferResult: this.pairs[0].value * 0.01
		}
	},
	methods: {
		calculate() {
			const val = this.pairs[this.pairIndex].value
			// point calculate
			const point = Math.abs(this.entry - this.stoploss) * this.float
			this.bufferResult = (this.volume * val * point) / 1000
			this.$emit("calculated", this.bufferResult)
		},
		volumeInputValueChanged(payload) {
			this.volume = payload
		}
	},
	watch: {
		volume(newValue) {
			localStorage.volume = newValue
			this.calculate()
		},
		point(newValue) {
			localStorage.point = newValue
			this.calculate()
		},
		pairIndex(newIndex) {
			localStorage.pairIndex = newIndex
			localStorage.pairFloat = this.pairs[newIndex].float
			this.entry = (this.entry * this.float) / this.pairs[newIndex].float
			this.stoploss = (this.stoploss * this.float) / this.pairs[newIndex].float
			this.float = this.pairs[newIndex].float
			this.calculate()
		},
		entry(newValue) {
			localStorage.entry = newValue
			this.calculate()
		},
		stoploss(newValue) {
			localStorage.stoploss = newValue
			this.calculate()
		}
	},
	computed: {},
	mounted: function() {
		if (localStorage.volume) {
			this.volume = localStorage.volume
		}
		if (localStorage.point) {
			this.point = localStorage.point
		}
		if (localStorage.pairIndex) {
			this.pairIndex = localStorage.pairIndex
			this.float = localStorage.pairFloat
		}
		if (localStorage.entry) {
			this.entry = localStorage.entry
		}
		if (localStorage.stoploss) {
			this.stoploss = localStorage.stoploss
		}
	}
}
</script>

