<template>
	<div class="control">
		<label for="point">Khối lượng</label>
		<input
			class="input"
			name="point"
			type="text"
			inputmode="decimal"
			autocomplete="off"
			v-model="volume"
			@focus="$event.target.select()"
		/>
		<div class="volume-wrapper">
			<button class="btn btn-primary" @click="volumeBtnClicked(-0.1)">
				–0.1
			</button>
			<button class="btn btn-primary" @click="volumeBtnClicked(-0.01)">
				–0.01
			</button>

			<button class="btn btn-primary" @click="volumeBtnClicked(0.01)">
				+0.01
			</button>
			<button class="btn btn-primary" @click="volumeBtnClicked(0.1)">
				+0.1
			</button>
		</div>
		<!-- <p class="input-note is-hidden-mobile">
			Dùng phím mũi tên lên/xuống để tăng/giảm 0.01.
		</p> -->
	</div>
</template>

<script>
export default {
	name: "VolumeInput",
	data() {
		return {
			volume: 0.01
		}
	},
	watch: {
		volume: function(newVolume) {
			this.$emit("volume-changed", newVolume)
		}
	},
	methods: {
		volumeBtnClicked(v, _) {
			if (this.volume + v > 0 || v > 0) {
				this.volume += v
			}
			this.volume = Number(this.volume.toFixed(2))
		}
	}
}
</script>

